import styles from './styles.module.css';
import Logo from "../../../assets/images/Logo.png";
import Estrela from "../../../assets/images/estrela.png";
import Prancheta from "../../../assets/images/prancheta.png";
import List from "../../../assets/images/list.png";
import Perfil from "../../../assets/images/perfil.png";

export function TopicOne() {
  return (
    <>
      <div className={styles.container}>
        <img className={styles.img} src={Estrela} alt="" />
        <h2 className={styles.textTitle}>AVALIAO MTUA</h2>
        <p className={styles.textSubTitle}>Aps o servio, tanto o profissional freelancer quanto o cliente podero avaliar o servio de ambos os lado.</p>
      </div>
    </>
  )
}

export function TopicTwo() {
  return (
    <>
      <div className={styles.container}>
        <img className={styles.img} src={Prancheta} alt="" />
        <h2 className={styles.textTitle}>ESCOPO DO PRODUTO</h2>
        <p className={styles.textSubTitle}>O usurio pode definir as funcionalidades iniciais do projeto para que o desenvolvedor j conhea o produto</p>
      </div>
    </>
  )
}

export function TopicTree() {
  return (
    <>
      <div className={styles.container}>
        <img className={styles.img} src={List} alt="" />
        <h2 className={styles.textTitle}>STATUS DO PROJETO</h2>
        <p className={styles.textSubTitle}>O cliente ter acesso ao status do projeto que est em desenvolvimento para que possa acompanhar o progresso do mesmo.</p>
      </div>
    </>
  )
}

export function TopicFour() {
  return (
    <>
      <div className={styles.container}>
        <img className={styles.img} src={Perfil} alt="" />
        <h2 className={styles.textTitle}>PERFIL PERSONALIZADO</h2>
        <p className={styles.textSubTitle}>Faa uma descrio sobre si mesmo, adicione suas skills, e exiba seu portfolio da forma que desejar em nossa plataforma.</p>
      </div>
    </>
  )
} 